/* font-family: "OpenSansRegular"; */
@font-face {
  src:
    url("../fonts/OpenSansRegular/OpenSansRegular.eot?#iefix") format("embedded-opentype"),
    url("../fonts/OpenSansRegular/OpenSansRegular.woff2") format("woff2"),
    url("../fonts/OpenSansRegular/OpenSansRegular.woff") format("woff"),
    url("../fonts/OpenSansRegular/OpenSansRegular.ttf") format("truetype");
  font-family: "OpenSansRegular";
  font-weight: 400;
  font-style: normal;
  font-display: fallback;
}

@font-face {
  src:
    url("../fonts/RobotoCondensedBold/RobotoCondensedBold.eot?#iefix") format("embedded-opentype"),
    url("../fonts/RobotoCondensedBold/RobotoCondensedBold.woff2") format("woff2"),
    url("../fonts/RobotoCondensedBold/RobotoCondensedBold.woff") format("woff"),
    url("../fonts/RobotoCondensedBold/RobotoCondensedBold.ttf") format("truetype");
  font-family: "RobotoCondensedBold";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

